import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import RackCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/rack/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/racks"

class CreateRack extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createRack } = this.props;
    createRack();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.rack.error) {
      notification("error", this.props.rack.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.rack.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { storeRack } = this.props;
    storeRack(value);
  };

  render() {

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="racks.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <RackCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            rack={this.props.rack}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    rack: state.rack
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateRack);
